import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'; // เพิ่ม import
import './RfaBim.css';

const RfaBim = () => {
    const navigate = useNavigate(); // เพิ่มการประกาศ hook
    const [data, setData] = useState([]);
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(true);
    const [userSites, setUserSites] = useState([]);
    const [selectedSite, setSelectedSite] = useState(null);

    // ดึงข้อมูล sites ก่อน
    useEffect(() => {
    const fetchSites = async () => {
        try {
            const token = localStorage.getItem('token');
            if (!token) {
                navigate('/'); // ตอนนี้สามารถใช้ navigate ได้แล้ว
                return;
            }

            console.log('Using token:', token); // Debug

            const response = await fetch('/api/rfa/user-sites', {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });

            console.log('Response:', response); // Debug

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.error || 'Failed to fetch sites');
            }

            const data = await response.json();
            console.log('Data received:', data); // Debug

            if (data.success) {
                setUserSites(data.sites);
                if (data.sites.length > 0) {
                setSelectedSite(data.sites[0].id);
                }
            }
        } catch (err) {
            console.error('Fetch error:', err);
            setError(err.message);
            if (err.message === 'No token found') {
                // Redirect to login if no token
                navigate('/');
            }
        }       
    };

    fetchSites();
}, [navigate]); // เพิ่ม navigate ใน dependencies

    // ดึงข้อมูลเอกสารเมื่อเลือก site
    useEffect(() => {
        if (!selectedSite) return;

        const fetchDocuments = async () => {
            try {
                setLoading(true);
                const token = localStorage.getItem('token'); // เพิ่มการดึง token จาก localStorage
                if (!token) {
                    navigate('/'); // Redirect to login if no token
                    return;
                }

                const response = await fetch(`/api/rfa/documents/${selectedSite}`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json'
                    }
                });
                const result = await response.json();
                
                if (result.success) {
                    setData(result.documents);
                } else {
                    setError(result.error || 'Failed to fetch documents');
                }
            } catch (err) {
                console.error('Error fetching documents:', err);
                setError('An error occurred while fetching documents');
            } finally {
                setLoading(false);
            }
        };

        fetchDocuments();
    }, [selectedSite, navigate]); // เพิ่ม navigate ใน dependencies

    if (loading) return <div>Loading...</div>;
    if (error) return <div className="error-message">{error}</div>;

    return (
        <div className="rfa-bim-container">
            <h1>RFA BIM</h1>
            
            {/* Site Selection */}
            <div className="site-selection">
                <select 
                    value={selectedSite || ''} 
                    onChange={(e) => setSelectedSite(e.target.value)}
                >
                    {userSites.map(site => (
                        <option key={site.id} value={site.id}>
                            {site.site_name}
                        </option>
                    ))}
                </select>
            </div>

            {/* Documents Table */}
            <div className="documents-table">
                <table>
                    <thead>
                        <tr>
                            <th>Document Number</th>
                            <th>Title</th>
                            <th>Status</th>
                            <th>Created Date</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {data.map((doc) => (
                            <tr key={doc.id}>
                                <td>{doc.document_number}</td>
                                <td>{doc.title}</td>
                                <td>{doc.status}</td>
                                <td>{doc.created_at}</td>
                                <td>
                                    <button className="view-btn" onClick={() => window.open(doc.file_url, '_blank')}>
                                        View
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default RfaBim;