import { BrowserRouter,Routes,Route } from "react-router-dom";
import ErrorBoundary from './components/ErrorBoundary';
import MainLayout from './components/Layout/MainLayout'; 
import Login from './pages/Login';
import UserDashboard from './pages/UserDashboard';
import RfaBim from './pages/RfaBim';
import RfaAdmin from './pages/RfaAdmin';
import Rfi from './pages/Rfi';
import WorkRequest from './pages/WorkRequest';

function App() {
  return (
    <BrowserRouter>
      <ErrorBoundary>
        <Routes>
          <Route path="/" element={<Login />} />
          <Route element={<MainLayout />}>
            <Route path="/dashboard" element={<UserDashboard />} />
            <Route path="/rfa-bim" element={<RfaBim />} />
            <Route path="/rfa-admin" element={<RfaAdmin />} />
            <Route path="/rfi" element={<Rfi />} />
            <Route path="/work-request" element={<WorkRequest />} />
          </Route>
        </Routes>
      </ErrorBoundary>  
    </BrowserRouter>
  );
}
export default App;